#page-wrap {
  text-align: center;

  /* Prevent sidebar from showing a scrollbar on page */
  overflow: auto;
}

/* Individual item */
.bm-item {
  padding: 5px;
  display: flex !important;
  text-decoration: none;
  color: #d1d1d1;
  transition: color 0.2s;
  line-height: 150%;
  transition: background-color 0.6s ease !important;
  background-color: transparent;
}

/* Change color on hover */
.bm-item:hover {
  transition: background-color 3s ease;
  background-color: rgba(172, 208, 197, 0.5) !important;
}


/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 30px;
  height: 24px;
  right: 8px;
  top: 8px;
  border: 9px solid #40564f;
  background-color: #40564f;
  border-radius: 20px;
  box-shadow: 0px 0px 10px 1px #344642;
  transition: ease-in-out 1s all;
}

.bm-item > svg {
  font-size: 30px;
}

.bm-burger-button.menuOpen {
  transform: rotate(1080deg);
  background: #00251f;
  border: 9px solid #00251f;
  box-shadow: 0px 0px 10px 1px #eaf1ef;
}

.menu-header {
  position: absolute;
  left: 43%;
  top: 7px;
  font-size: 80%;
}

.bm-icon {
  width: 39px !important;
  height: 39px !important;
  position: relative;
  left: 0;
  top: -8px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: white;
}

.bm-burger-bars:first-of-type {
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}

.bm-burger-bars:nth-of-type(3) {
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
}

.bm-burger-bars:first-of-type,
.bm-burger-bars:nth-of-type(3) {
  background: #cbfefb !important;
}

.bm-burger-bars:nth-of-type(2) {
  border-radius: 100%;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}

/* General sidebar styles */
.bm-menu {
  background: #373a47;
  padding: 40px 0;
  font-size: 1.15em;
  background: linear-gradient(180deg,#344642 42px, rgb(69, 93, 89) 42px, #415652 113px, #5b7a75 113px, #577b6f 184px, #629788 184px, #629788 257px, #62a592 257px, #62a592 329px, #69b19d 329px, #e5edeb);
  /* background: linear-gradient(180deg, #629788, #f4f5f5); */
  box-shadow: 1px 1px 10px 0px #ffffffb0;
  overflow: hidden !important;
  font-family: "sunday";
}

/* .bm-menu-wrap,
.bm-item {
  transition: transform 0.6s cubic-bezier(0.7, 0, 0.3, 1) 0s !important;
} */

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}


/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}


.menu-item,
.menu-item--active {
  height: 60px;
  width: 100%;
}

.menu-item {
  border-top: 1px solid white;
  border-bottom: 1px solid white;
  background-color: unset !important;
  align-items: center;
  color: white;
  cursor: pointer;
  display: flex;
  font-size: 19px;
  /* justify-content: center; */
  letter-spacing: 1px;
  text-align: center;
}

.button-text {
  position: fixed;
  right: 65px;
  top: 29px;
  z-index: 100000;
  width: 73px;
  display: flex;
  background-color: #40564fcc;
  padding: 5px 0;
  border-radius: 87px;
  font-family: 'Shadows Into Light', cursive;
  line-height: 100%;
  justify-content: center;
  align-items: center;
  font-size: 130%;
  transition: opacity 3s linear;
}

.button-text:after {
  content: '';
  position: absolute;
  right: 0;
  top: 32%;
  width: 0;
  height: 0;
  border: 14px solid transparent;
  border-left-color: #40564fcc;
  border-right: 0;
  border-top: 0;
  margin-top: -2px;
  margin-right: -12px;
  transform: rotate(-8deg);
  display: flex;
}

/* .one-word-per-line {
    word-spacing: <parent-width>;
} */

.button-text-inner {
   width: min-intrinsic;
   width: -webkit-min-content;
   width: -moz-min-content;
   width: min-content;
   display: table-caption;
   display: -ms-grid;
   -ms-grid-columns: min-content;
 }

 @media only screen and (min-width: 401px) {
   .bm-item {
     max-width: 290px;
   }

   .bm-menu-wrap {
     max-width: 300px;
   }
 }

 @media only screen and (max-width: 401px) {
   .bm-item {
     max-width: 240px;
   }

   .bm-menu-wrap {
     max-width: 250px !important;
   }
 }
